/* Import custom fonts */
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}
/* Global Styles to control margin and padding */
html, body {
  margin: 0;
  padding: 0;
}

section {
  margin-top: 2rem;  /* Reduced margin top */
  margin-bottom: 2rem; /* Reduced margin bottom */
}

.container {
  padding-left: 1rem;
  padding-right: 1rem;
}

h1, h2, h3 {
  margin-bottom: 1rem;  /* Reduced bottom margins on headings */
}

button, a {
  margin: 0.5rem;  /* Reduced margin for buttons and links */
}

* {
  box-sizing: border-box; /* Prevents padding from affecting layout width */
}


section {
  margin-top: 1rem; 
}
h1, h2 { 
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block; 
  font-family: 'Dancing Script', cursive;
}

.my-custom-class {
  @apply text-pink-800; 
}

.bg-primary-color {
  @apply px-1 py-2 w-full rounded-xl mr-4 bg-gradient-to-r from-orange-500 to-pink-500 font-bold hover:border-none;
}

#Contact {
  min-height: 100vh;
  padding-top: 0.5rem; 
}

@media (min-width: 768px) {
  .section-gap {
    margin-top: 0.5rem; 
  }
}

@media (max-width: 767px) {
  .section-gap {
    margin-top: 0.5rem; 
  }
}
